body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

::-webkit-scrollbar-button {
  background-image: url("");
  background-repeat: no-repeat;
  width: 5px;
  height: 0px;
}

::-webkit-scrollbar-track {
  background-color: transparent;
}

::-webkit-scrollbar-thumb {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  background-color: #95e1d3;
}

::-webkit-scrollbar-thumb:hover {
  background-color: #60d2bd;
}

::-webkit-resizer {
  background-image: url("");
  background-repeat: no-repeat;
  width: 4px;
  height: 0px;
}

::-webkit-scrollbar {
  width: 7px;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.header {
  background-image: url("../img/main-banner-bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.header__items {
  display: flex;
  justify-content: space-between;
  padding: 30px 0;
}

.header__logo a {
  font-size: 30px;
  color: #eaffd0;
}

.header__btn {
  margin: 50px auto 150px;
  padding: 10px 30px;
  display: block;
  width: 162px;
  box-sizing: border-box;
  background: transparent;
  border: 2px solid #eaffd0;
  text-transform: uppercase;
  font-size: 14px;
  color: #eaffd0;
  transition: background .3s;
}

.header__btn:hover {
  background: rgba(255, 255, 255, 0.2);
}

.menu__list {
  display: flex;
  font-size: 14px;
  text-transform: uppercase;
}

.menu__item a {
  position: relative;
  padding: 10px 15px;
  display: block;
  color: #eaffd0;
  transition: all .3s;
}

.menu__item a:hover {
  color: #fce38a;
}

.menu__item a:hover:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -32%;
  width: 64%;
  height: 2px;
  background: #fce38a;
}

h1 {
  position: relative;
  margin: 20px 0 50px;
  padding-bottom: 40px;
  text-align: center;
  text-transform: uppercase;
  font-size: 120px;
  line-height: 0.95;
  color: #eaffd0;
}

h1:after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  margin-left: -30px;
  width: 60px;
  height: 2px;
  background: #eaffd0;
}

h2 {
  margin: 70px 0 20px;
  text-align: center;
  font-size: 72px;
  color: #eaffd0;
}

h4 {
  font-size: 14px;
  text-transform: uppercase;
  color: #333333;
}

.banner-slider {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin-top: 150px;
}

.banner-slider__item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 3 - 30px);
  text-transform: uppercase;
  font-size: 18px;
  color: #eaffd0;
}

.banner-slider__item span {
  font-size: 24px;
}

.banner-slider__item {
  padding: 25px 0;
  border-top: 2px solid #eaffd0;
}

h3 {
  margin: 10px 0 40px;
  text-align: center;
  text-transform: uppercase;
  font-size: 30px;
  color: #333333;
}

h3:after {
  content: "";
  margin: 40px auto 40px;
  display: block;
  width: 60px;
  height: 2px;
  background: #f38181;
}

.h3sub {
  margin: 0 0 10px;
  padding-top: 80px;
  text-align: center;
  font-size: 24px;
  color: #333333;
}

.h3about {
  margin: 40px 0 100px;
  text-align: center;
  font-size: 15px;
  color: #999999;
}

.about {
  margin-bottom: 100px;
}

.about .team {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.about .team__item {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
}

.counter {
  background: #95e1d3;
}

.counter__wrapper {
  display: flex;
}

.counter__item {
  padding: 80px 0;
  width: 20vw;
  text-align: center;
  color: #fff;
  border-right: 1px solid #fff;
}

.counter__item:first-child {
  border-left: 1px solid #fff;
}

.counter__item .amount {
  display: block;
  font-size: 72px;
}

.counter__item .description {
  text-transform: uppercase;
  font-size: 14px;
}

.service__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
  margin: 40px 0 120px;
}

.service__wrapper .block {
  word-wrap: break-word;
  padding-left: 15px;
  padding-right: 15px;
  width: calc(100% / 12 * 4 - 30px);
  margin-top: 40px;
  padding-bottom: 40px;
  display: flex;
}

.service__wrapper .block:nth-child(-n+3) {
  border-bottom: 1px solid #e5e5e5;
}

.service__wrapper .block__img {
  width: 30%;
  text-align: center;
}

.service__wrapper .block__img img {
  width: 40px;
}

.service__wrapper .block__info {
  width: 70%;
}

.service__wrapper .block__paragraph {
  padding-top: 10px;
  font-size: 14px;
  line-height: 22px;
  color: #999999;
}

.design {
  margin-bottom: 80px;
  background-image: url("../img/design-banner-bg.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
}

.design__imgs {
  position: relative;
  text-align: center;
}

.design__img-tablet {
  margin-bottom: -4px;
}

.design__img-phone {
  position: absolute;
  bottom: 0;
  margin-bottom: -90px;
  left: 50%;
  margin-left: 100px;
}

.offer {
  margin-bottom: 120px;
}

.offer__wrapper {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

.offer__img {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 6 - 30px);
}

.offer__accordion {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 15px;
  margin-right: 15px;
  width: calc(100% / 12 * 6 - 30px);
}

.accordion {
  margin-bottom: 5px;
}

.accordion__header {
  padding: 15px 0;
  display: flex;
  align-items: center;
  border: 1px solid #cccccc;
}

.accordion__header img {
  padding: 0 15px;
}

.accordion__header i {
  margin-right: 0;
  margin-left: auto;
  padding: 0 15px;
  color: #cccccc;
  font-size: 24px;
}

.accordion__info {
  display: none;
  padding: 20px 25px 25px 20px;
  border: 1px solid #cccccc;
  border-top: 0;
  font-size: 15px;
  line-height: 24px;
  color: #999999;
}

.accordion__info_active {
  display: block;
}

.accordion__info p {
  padding-right: 20px;
  height: 135px;
  overflow: scroll;
}

@media screen and (max-width: 1200px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (max-width: 780px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media screen and (max-width: 576px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}

@media screen and (max-width: 420px) {
  .container {
    padding-left: 10px;
    padding-right: 10px;
  }
}